<style>

@media screen and (max-width: 1024px) {
  #select-qcollection {
    box-sizing: border-box;
  }
}

@media screen and (min-width: 1025px) {
  #select-qcollection {
    box-sizing: border-box;
    padding-left: 240px
  }
}

.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .3s ease;
}

#select-qcollection .modal-container {
  width: 300px;
  margin: 130px auto 0;
  padding: 0;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
}

.modal-header {
  padding: 20px 0px 0 20px;
  align-items: center
}

.modal-header h4 {
  margin: 0;
  color: #2196F3;
}

.modal-body {
  margin: 20px 0;
  margin-bottom: 0;
  max-height: 360px;
  overflow: scroll;
}

.modal-body::-webkit-scrollbar {
  -webkit-appearance: none;
  width: 6px;
}
.modal-body::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background-color: #ccc;
  -webkit-box-shadow: 0 0 1px rgba(255,255,255,.5);
}

.modal-footer{
  padding-right: 20px;
  padding-bottom: 10px
}

.modal-enter, .modal-leave {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.list {
  padding: 0px;
  list-style: none;
  margin:0
}
.list li {
  align-items: center;
  padding: 15px 20px;
  font-size: 16px;
  cursor: pointer;
  background-image: linear-gradient(to bottom, #9E9E9E 0%, #9E9E9E 51%, transparent 51%);
  background-size: 100% 1px;
  background-repeat: no-repeat;
}

.list li:active {
  background-color: #eee
}

.list .subject-label {
  color: #E91E63;
  border: 1px solid #E91E63;
  padding: 2px 4px;
  font-size: 13px;
  margin-right: 8px
}

</style>
<template>
  <div id="select-qcollection" class="modal-mask" v-show="show" transition="modal">
    <div class="modal-container">
      <div class="modal-header flex-row">
        <h4>加入題集</h4>
        <span class="flex" style="flex: 1"></span>
        <mdl-button class="close" @click="closeModal()">
          <i class="material-icons">close</i>
        </mdl-button>
      </div>
      <mdl-spinner :active="spinnerActive" v-show="spinnerActive"></mdl-spinner>
      <div class="modal-body">
        <ul class="list">
          <li class="flex-row" v-for="qc in myQcollections" @click="saveOneToCollection(qc._id)">
            <span class="subject-label">{{qc.subject | subject}}</span>
            <span>{{qc.name}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      spinnerActive: true,
      myQcollections: []
    }
  },
  props: ['show', 'qid'],
  methods: {
    closeModal: function () {
      this.show = false
    },
    saveOneToCollection: function (qcollection_id) {
      if (qcollection_id && this.qid) {
        let data = {
          qcollection_id: qcollection_id,
          question_id: this.qid
        }
        this.$http.post('/api/manage-qcollection/add-question', data).then(function (response) {
          this.show = false
          this.showToast('添加成功')
        }, function (response) {
          this.showToast('失敗')
          console.log(response)
        })
      } else {
        this.showToast('發生錯誤')
      }
    },
    getMyQcollectionLists: function () {
      if (this.myQcollections.length === 0) {
        this.$http.get('/api/manage-qcollection/mine').then(function (response) {
          this.myQcollections = response.data
          this.spinnerActive = false
        }, function (response) {
          console.log(response)
        })
      }
    },
    showToast: function (message) {
      this.$dispatch('show-toast', message)
    }
  },
  events: {
    'getMyQcollectionLists': function () {
      this.getMyQcollectionLists()
    }
  }
}
</script>



